import server from '../../server.js';
export default {
	data() {
		return {
			alipay:false,
			aliuser:'',
			fee:0,
			openid:''
		}
	},
	onLoad:function(e){
		console.log(e.type)
		if(e.type=="alipay"){
			this.alipay=true;
		}
	},
	methods: {
		alipayuser:function(e){
			this.aliuser=e.detail.value;
		},
		feeing:function(e){
			this.fee=e.detail.value;
		},
		tixian:function(){
			if(this.alipay){
				this.openid=this.aliuser
			}else{
				this.openid='wxpay'
			}
			uni.showLoading({
				title: '正在请求'
			});
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/user.php?type=tixian", //请求标记已读消息
				data: {
					'openid':server.userinfo.wxOpenId,
					'fee':this.fee
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Cookie':server.cookie,
					'system':server.system
				},
				success: (res) => {
					console.log(res)
					if(res.data.code=="1"){
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						})
						setTimeout(function () {
							uni.hideLoading()
							uni.navigateBack({});
						}, 2000);
					}else{
						uni.showToast({
							title: res.data.msg,
							position:'bottom',
							icon:'none'
						})
					}
					
				},
				complete() {
					
				}
			});
			
			
		}
	}
}
